// ==========================================================================
// col.scss
// ==========================================================================

// Set width according to screen size.
.col-xs-12  { width: 100%; }
.col-sm-6   { @include mq_min($breakpoint--sm) { width: 50%; } }
.col-md-12  { @include mq_min($breakpoint--md) { width: 100%; } }
.col-md-6   { @include mq_min($breakpoint--md) { width: 50%; } }
.col-md-4   { @include mq_min($breakpoint--md) { width: 33.33%; } }
.col-md-3   { @include mq_min($breakpoint--md) { width: 25%; } }
.col-lg-4   { @include mq_min($breakpoint--lg) { width: 33.33%; } }
.col-lg-3   { @include mq_min($breakpoint--lg) { width: 25%; } }

// Add padding for gutter.
.col-xs-12, .col-sm-6, .col-md-12, .col-md-6, .col-md-4, .col-md-3, .col-lg-4, .col-lg-3 { 
    @extend %padding-horizontal; 
}